@import"https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap";.container{font-family:"Press Start 2P",cursive;text-align:center;max-width:1000px;margin:2rem auto;padding:2rem;color:#fff;background-color:#000;border:6px solid #fff;box-shadow:0 0 20px hsla(0,0%,100%,.3)}.title{font-size:2.5rem;margin-bottom:2rem;text-shadow:0 0 10px #fff;letter-spacing:2px}.status{font-size:1.5rem;margin:2rem 0;min-height:36px;text-shadow:0 0 5px #fff}.board{display:grid;grid-template-columns:repeat(3, 1fr);gap:8px;margin:2rem auto;width:500px;height:500px}.cell{position:relative;background-color:#000;border:3px solid #fff;cursor:pointer;aspect-ratio:1;min-height:0;padding:0;transition:all .2s ease}.cell:hover:not(:disabled){background-color:hsla(0,0%,100%,.1)}.cell:disabled{cursor:not-allowed}.cell span{position:absolute;top:0;left:0;width:100%;height:100%;display:flex;align-items:center;justify-content:center;font-size:5rem;line-height:1}.cell.winningCell span{color:lime;text-shadow:0 0 15px lime;animation:pulse .5s infinite alternate}.resetButton{margin-top:2rem;padding:1rem 2rem;font-family:inherit;font-size:1.2rem;background-color:#000;color:#fff;border:3px solid #fff;cursor:pointer;transition:all .3s ease;text-transform:uppercase}.resetButton:hover{background-color:hsla(0,0%,100%,.568);color:#000;transform:scale(1)}@keyframes pulse{from{opacity:.8}to{opacity:1}}.container::after{content:"";position:fixed;top:0;left:0;width:100%;height:100%;background:repeating-linear-gradient(0deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8) 1px, transparent 1px, transparent 2px);pointer-events:none;z-index:-1}